document.addEventListener("DOMContentLoaded", () => {
  const slides = document.querySelectorAll(".carousel__inner__slide");

  const slideDelay = 4;
  const slideDuration = 1;
  let slideNumber = 0;
  let initialCount = 0;

  const timer = gsap.delayedCall(slideDelay, animate);
  const mainTl = gsap.timeline();
  const progressTl = gsap.timeline();

  //initial progress animation
  updateProgress();
  ++initialCount;

  function animate() {
    timer.restart(true);
    let prevSlideNumber = slideNumber;
    slideNumber = (slideNumber + 1) % slides.length;

    updateProgress(prevSlideNumber);

    if (slideNumber === 0) {
      mainTl.set(".slide-0", { zIndex: 2 });
    }

    if (slideNumber === 1) {
      mainTl.set(".slide-0", { zIndex: 1 });
    }

    mainTl.to(`.text-${prevSlideNumber}`, { opacity: 0, duration: 1, zIndex: 10, ease: "power4.inOut" });
    mainTl.fromTo(
      `.slide-${slideNumber}`,
      { left: "100%" },
      {
        duration: slideDuration,
        left: 0,
        ease: "power4.inOut",
        onComplete: () => {
          mainTl.set(`.slide-${prevSlideNumber}`, { left: "100%" });
        },
      },
      "<"
    );
    mainTl.to(`.text-${slideNumber}`, { opacity: 1, duration: 1, zIndex: 15, ease: "power4.inOut" });
  }

  function updateProgress(prevSlide) {
    if (slideNumber === 0 && initialCount > 0) {
      progressTl.set(".progress", { width: 0 });

      gsap.to(`.btnNumber-${Math.floor((slides.length - 1) / 2)}, .btnText-${Math.floor((slides.length - 1) / 2)}`, { opacity: 0.5 });
      gsap.to(`.btnNumber-0, .btnText-0`, { opacity: 1 });
    } else {
      gsap.to(`.btnNumber-${Math.floor(prevSlide / 2)}, .btnText-${Math.floor(prevSlide / 2)}`, { opacity: 0.5 });
      gsap.to(`.btnNumber-${Math.floor(slideNumber / 2)}, .btnText-${Math.floor(slideNumber / 2)}`, { opacity: 1 });
    }

    const progress = ((slideNumber + 1) / slides.length) * 100;
    progressTl.to(".progress", { width: progress + "%", ease: "none", duration: slideDelay - 1 + slideDuration });
  }
});
